<template>
  <div>
    <van-nav-bar title="會員登入" left-arrow @click-left="$router.go(-1)" />
    <div class="form">
      <div class="title">
        <h2>手機登入</h2>
        <p>未註冊的手機登入後將自動註冊</p>
      </div>
      <div class="usernumber marginB20">
        <input v-model="mobile" type="text" placeholder="請輸入手機號碼" />
      </div>
      <div class="userchickJpg flexs marginB20">
        <input v-model="picCode" type="text" placeholder="請輸入圖形驗證碼" />
        <img v-show="picUrl" @click="createdGetUrl" class="point" :src="picUrl" alt="" />
      </div>
      <div class="userchick flexs">
        <input v-model="smgCode" type="text" placeholder="請輸入短信驗證碼246810" />
        <button v-if="!timer" @click="getCode" class="chickButton point">發送驗證碼</button>
        <button v-else class="chickButton2">{{`請等待${second}秒`}}</button>
      </div>
      <van-button @click="login" round block type="info" native-type="submit" class="submit">提交</van-button>
    </div>
  </div>
</template>

<script>
import { getLoginApi, getMsgCode, codeLogin } from '@/api/login'
export default {
  name: 'loginIndex',
  data () {
    return {
      // 驗證碼圖
      picCode: '', // 你輸入的圖形驗證碼
      picKey: '',
      // 驗證碼短信計時
      totalSecond: 5,
      second: 5,
      timer: null,
      // 使用者輸入
      mobile: '0912345678', // 手機號
      picUrl: '', // 圖形驗證碼
      smgCode: '246810' // 短信驗證碼
    }
  },
  created () {
    this.getUrl()
    this.createdGetUrl = this.getButton()
  },
  destroyed () {
    clearInterval(this.timer)
  },
  methods: {
    // 驗證碼圖防抖
    getButton () {
      let timeOut
      return () => {
        clearTimeout(timeOut)
        timeOut = setTimeout(async () => {
          await this.getUrl()
          this.$toast('驗證圖片已更新')
        }, 300)
      }
    },
    // 驗證碼圖API
    async getUrl () {
      const { data: { base64, key } } = await getLoginApi()
      this.picUrl = base64
      this.picKey = key
    },
    // 驗證手機號與圖形驗證碼
    validFn () {
      if (!/^09\d{8}$/.test(this.mobile)) {
        this.$toast('請輸入正確的手機號碼')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        this.$toast('請輸入正確的圖形驗證碼')
        return false
      }
      return true
    },
    // 驗證碼短信
    async getCode () {
      if (!this.validFn()) {
        return
      }
      if (!this.timer && this.second === this.totalSecond) {
        const resMsgCode = await getMsgCode(this.picCode, this.picKey, this.mobile)
        this.timer = setInterval(() => {
          this.second--
          if (this.second <= 0) {
            clearInterval(this.timer)
            this.timer = null
            this.second = this.totalSecond
          }
        }, 1000)
        this.$toast(resMsgCode.message)
      }
    },
    // 登入
    async login () {
      if (!this.validFn()) {
        return
      }
      if (!/^\d{6}$/.test(this.smgCode)) {
        this.$toast('短信驗證碼錯誤')
        return
      }
      const res = await codeLogin(this.mobile, this.smgCode)
      this.$store.commit('user/setUserInfo', res.data)
      this.$toast('登入成功')
      const url = this.$route.query.backUrl || '/'
      this.$router.replace(url)
    }
  }
}
</script>

<style lang="less" scoped>
.flexs {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.marginB20 {
  margin-bottom: 20px;
}
input {
  padding: 10px 5px;
  border: 1px solid #e7e7e7;
  width: 75%;
}
.point{
  cursor: pointer;
}
.form {
  font-size: 12px;
  width: 100vw;
  padding: 20px;
  display: flex;
  flex-direction: column;
  .title {
    margin-bottom: 40px;
    text-align: left;
    p {
      color: #999;
    }
  }
  .usernumber {
    display: flex;
    justify-content: left;
  }
  .userchickJpg {
    img {
      height: 20px;
    }
  }
  .userchick {
    .chickButton {
      background-color: transparent;
      border: 0px;
      color: orange;
    }
    .chickButton2 {
      background-color: transparent;
      border: 0px;
      color: rgb(131, 121, 104);
    }
  }
  .submit {
    margin-top: 40px;
  }
  .van-button--info {
      color: #fff;
      background: linear-gradient(90deg, rgb(255, 216, 161) 20%, rgb(255, 167, 43) 60%);
      border: 1px solid #ffaa00;
    }
}
</style>
